// 定义一个函数，当按钮被点击时执行
function handleClick() {
    // 在控制台打印消息
    console.log('按钮被点击了！');
    alert('按钮被点击了！');
  }
  
  // 假设页面上有一个id为'myButton'的按钮
  // 使用document.getElementById获取按钮元素
  const button = document.getElementById('myButton');
  
  // 为按钮添加点击事件监听器
  button.addEventListener('click', handleClick);